code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
  /* scroll-snap-type: y mandatory; */
  max-width: 98.8vw;
}

.home {
  color: whitesmoke;
  font-family: "Roboto", sans-serif;
  margin: 0;
  padding: 0;
  background-color: rgb(255,255,255);
  background: linear-gradient(180deg, rgba(255,255,255,1) 0%, rgba(238,238,238,1) 34%, rgba(255,255,255,1) 53%, rgba(0,0,0,1) 71%, rgba(0,0,0,1) 100%);
  background-image: url(/static/media/Caparuido.f188d782.png), repeat;
}

main {
width: 100vw;
margin: 0;
padding: 0;
}


.toggle-btn {
  position: fixed;
  top: 5%;
  left:5%;
  cursor: pointer;
  z-index: 5000;
}

.toggle-btn span{
 display: block;
 width: 5vh;
 height: 5vh;
 background-color: lawngreen;
 border-radius: 50%;
}

.nav {
  width:40%;
  height: 100vh;
  position: fixed;
  z-index: 3;
  background-color: rgba(199, 199, 199, 0.8);
  left: -40%;
  transition: 200ms all;
}

.nav-active {
  width:40%;
  height: 100vh;
  position: fixed;
  z-index: 1;
  background-color: rgba(199, 199, 199, 0.8);
  left: 0;
  transition: 200ms all;
}

.nav-content {
  height: 100%;
  width: 90%; 
  align-items: center;
}


.nav-links ul {
  display: flex;
  flex-direction: column;
  text-align: left;
  height: 100%;
  margin-top: 20vh;
}

.nav-links li {
  list-style: none;
  display: inline-block;
  margin: calc(100vh / 50) 5vw;
}

.nav-links li a {
  text-decoration: none;
  font-size: 2rem;
  font-weight: 300;
  color: whitesmoke;
}

.nav-links li a:hover {
  color: rgb(41, 41, 41);
}

section {
  height: 100vh;
  width: 100vw;
  scroll-snap-align: center;
}


@media screen and (max-width: 450px) {
  .nav-links li a {
    text-decoration: none;
    font-size: 1.5rem;
    font-weight: 300;
    color: whitesmoke;
  }
}



.serv {
  transition: 0.3 ease all;
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.315);
  align-items: center;
  display: flex;
  flex-direction: row;
}



